import { Component, OnInit } from '@angular/core';
import { DataServiceService } from 'src/app/services/data-service.service';
import { GlobalDataSummary } from 'src/app/models/globalData';
import { CountryapiService } from '../../services/countryapi.service'
import { Observable, merge } from 'rxjs';
import { Country } from 'src/app/models/countryData';
import { DateWiseData } from 'src/app/models/date-wise-data';
import { DeathWiseDate } from 'src/app/models/date-wise-death'
import { RecWiseDate } from 'src/app/models/date-wise-rec'
@Component({
  selector: 'app-countries',
  templateUrl: './countries.component.html',
  styleUrls: ['./countries.component.css']
})
export class CountriesComponent implements OnInit {

  data: GlobalDataSummary[];
  countries: string[] = [];
  country$: Observable<Country[]>
  totalConfirmed = 0;
  totalActive = 0;
  totalDeaths = 0;
  totalRecovered = 0;
  dateWiseData;
  dateWiseDeathData;
  dateWiseRecData;
  checkSelectedDateWise: DateWiseData[];
  checkSelectedDateDeathData: DeathWiseDate[];
  checkSelectedDateRecData: RecWiseDate[];


  constructor(private service: DataServiceService, private api: CountryapiService) { }

  ngOnInit(): void {
    this.service.getDateWiseData().subscribe(
      (result) => {
        this.dateWiseData = result;
      }
    )

    this.service.getDateWiseGlobalDeath().subscribe(
      (result) => {
        this.dateWiseDeathData = result;
      }
    )

    this.service.getDateWiseGlobalRec().subscribe(
      (result) => {
        this.dateWiseRecData = result;
      }
    )

    this.service.getGlobalData().subscribe(result => {
      this.data = result;
      this.data.forEach(cs => {
        this.countries.push(cs.country)
      })
    })

    this.country$ = this.api.getAllCountries();


  }

  updateValues(country: string) {
    console.log(country)
    this.data.forEach(cs => {
      if (cs.country == country) {
        this.totalActive = cs.active
        this.totalDeaths = cs.deaths
        this.totalRecovered = cs.recovered
        this.totalConfirmed = cs.confirmed
      }
    })

    this.checkSelectedDateWise = this.dateWiseData[country]
    this.checkSelectedDateDeathData = this.dateWiseDeathData[country]
    this.checkSelectedDateRecData = this.dateWiseRecData[country]
    //console.log(this.checkSelectedDateWise)
  }

  public trackByFunction(index, country) {
    if (!country) return null;
    return index
  }

}





