import { Component, OnInit, Input } from '@angular/core';
import { DataServiceService } from '../../services/data-service.service'
import { GlobalDataSummary } from 'src/app/models/globalData';



@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  dataTable = [];
  totalConfirmed = 0;
  totalActive = 0;
  totalDeaths = 0;
  totalRecovered = 0;
  selection: string
  caseNumber = 100000;
  globalData: GlobalDataSummary[];
  chart = {
    PieChart: "PieChart",
    LineChart: "LineChart",
    height: 900
  }
  LineOptions = {
    animation: {
      duration: 500,
      easing: 'inAndOut',
    },
    legend: 'none',
    bar: { groupWidth: "95%" },
    colors: ['#28df99']
  }
  pieOptions = {
    animation: {
      duration: 500,
      easing: 'inAndOut'
    },
    pieHole: 0.4,
    sliceVisibilityThreshold: .02
  }

  constructor(private dataService: DataServiceService) { }

  initChart(caseType: string) {
    this.dataTable = [];
    //this.dataTable.push(["Country", "Cases"])

    this.globalData.forEach(cs => {
      let value: number
      if (caseType == "con") {
        if (cs.confirmed > this.caseNumber) {
          value = cs.confirmed
        }
      }
      if (caseType == "dth") {
        if (cs.deaths > this.caseNumber) {
          value = cs.deaths
        }
      }
      if (caseType == "rec") {
        if (cs.recovered > this.caseNumber) {
          value = cs.recovered
        }
      }
      if (caseType == "act") {
        if (cs.active > this.caseNumber) {
          value = cs.active
        }
      }
      this.dataTable.push([
        cs.country, value
      ])
    })
  }



  ngOnInit(): void {
    this.dataService.getGlobalData()
      .subscribe({
        next: (result) => {
          console.log(result);
          this.globalData = result;
          result.forEach(cs => {
            if (!Number.isNaN(cs.confirmed)) {
              this.totalActive += cs.active
              this.totalConfirmed += cs.confirmed
              this.totalDeaths += cs.deaths
              this.totalRecovered += cs.recovered
            }
          })

          this.initChart("con");
        }
      }
      )
  }

  updateChart(input: HTMLInputElement) {
    console.log(input.value);
    this.selection = input.value
    this.initChart(input.value)
    return this.selection
  }

  updateCaseNumber(input: HTMLInputElement) {
    console.log(this.caseNumber)
    console.log(input.value);
    var value = parseInt(input.value)
    this.caseNumber = value

    return this.caseNumber
  }
}
